.about-us {
  &__content {
    margin-bottom: 92px;

    @include on-tablet {
      margin-bottom: 132px;
    }

    @include on-desktop {
      margin-bottom: 164px;
    }
  }

  &__title {
    margin-bottom: 32px;
    text-align: left;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
  }
}
